%container{
    margin-right: auto;
    margin-left: auto;
    max-width: $max-width;
    @media screen and (max-width: $max-width - 1px){
        width: calc(100% - 2rem);
        margin-right: 1rem;
        margin-left: 1rem;
    }
}

%list{
    list-style: none;
    padding: 0;
    margin: 0;
}